<template>
  <div>
    <v-layout row justify-center>
      <v-dialog v-model="dialog"  max-width="600px">
        <!-- <template v-slot:activator="{ on }">
          <v-btn color="primary" flat dark v-on="on"> + 创建管理/代理人员账号</v-btn>
        </template>-->
        <v-card>
          <v-card-title>
            <span class="headline">时间颗粒度详细信息</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-data-table :headers="headers" :items="desserts" class="elevation-1" hide-actions>
                <template v-slot:items="props">
                  <td class="text-xs-center">{{ props.item.start_time | time }}</td>
                  <td class="text-xs-center">{{ props.item.strength }}</td>
                </template>
              </v-data-table>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <!-- <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn> -->
            <v-btn color="info darken-1" @click="dialog = false">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      data: [],
      headers: [
        {
          text: "时间颗粒度",
          align: "center",
          sortable: false,
          value: "name"
        },
        { text: "勃起力度( g )", value: "fat",align: "center", sortable: false }
      ],
      desserts: []
    };
  },
  // 更新完成
  mounted: function() {},
  methods: {
    dialogShow(data) {
      this.dialog = data.state;
      this.desserts = data.list;
    }
  }
};
</script>

<style lang="less" scoped>
.root_p {
  font-size: 16px;
}
</style>
