 * @Description: 设备列表
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-30 12:52:55
 * @LastEditTime: 2019-09-27 15:33:40
 -->
<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items"/>
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <!-- <v-flex xs6 sm4 md2>
            <v-text-field label="账号"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-text-field label="手机号码"></v-text-field>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="loader_"> 搜索 </v-btn> -->
        </v-layout>
      </v-card-title>
      <!-- 
        headers : 表头
        items： 表格内容
        total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
        disable-initial-sort 在初始渲染时禁用默认排序
        :hide-actions="true" 隐藏表格操作
        :dark="true" 暗主题
        :loading="true" 加载进度条
      -->
      <v-data-table
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :total-items="allList"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template v-slot:items="props">
          <td>{{ props.item.user_name }}</td>
          <td class="text-xs-centre">{{ props.item.time | time }}</td>
          <td class="text-xs-centre">{{ props.item.score }}</td>
          
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <!-- <v-alert :value="true" color="warning" icon="priority_high" outline>没有更多数据了 :(</v-alert> -->
          <p class="p_text" > 没有更多数据了 :( </p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>总计：</strong>
          </td>
        </template> -->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </v-card>

    <Module1 ref="module1" ></Module1>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Module1 from './views/module1.vue'
export default {
  components: {
    Breadcrumbs,
    Module1
  },
  data() {
    return {
        user_id:"",
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      loader: false,
      page: 0,
      allList: 0,
      items: [
        {
          text: "用户列表",
          disabled: false,
          to: "/userList"
        },
        {
          text: "EHS记录",
          disabled: false,
          to: "/EHS"
        }
      ],
      headers: [
        { text: "用户", align: "left", sortable: false, value: "name"},
        { text: "创建时间", value: "calories", sortable: false },
        { text: "分数", value: "fat", sortable: false }
      ],
      desserts: []
    };
  },
  mounted: function() {
  },
  methods: {
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    // 时间颗粒度
    rlist( data ){
        let datas = {
        state:true,
        list: data
      }
      this.$refs.module1.dialogShow(datas);
    },
    info(event) {
      this.loader = true;
        this.user_id = this.$route.query.user_id-0;
      // descending: false, page: 1, rowsPerPage: 20, sortBy: null, totalItems: 0}
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_ehs,
        data: {
          "page_count": event.rowsPerPage,
          "page": event.page-1,
          "user_id": this.user_id
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas.list;
            this.allList = response.data.datas.total_number;
          } else {
            this.$Dialog.getProductCheckResult_({
                text: response.data.message,
                timeout: 3000,
                snackbar: true
            })
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
                text: error,
                timeout: 3000,
                snackbar: true
            })
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
  padding-right: 10px;
  padding-left: 10px;
}
</style>
